/* General Hero Video */
.about-hero-video {
  position: relative;
  height: 63.7vh;
  overflow: hidden;

  clip-path: polygon(
    0 0,
    100% 0,
    100% 90%,
    0 100%
  );
  .hero-video {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .hero-overlay {
    position: absolute;
    top: 0; left: 0;
    width: 100%; height: 100%;
    background: rgba(0,0,0,0.5);
  }
  .hero-content {
    position: absolute;
    top: 50%; left: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
    /* text-align: center; */
    h1 { font-size: 3rem; span { color: #e0d7b1; } }
    .hero-btn {
      margin-top: 20px;
      padding: 12px 28px;
      color: #000;
      border: none;
      font-weight: 600;
      border-radius: 6px;
      cursor: pointer;
    }
  }
}

/* Sections */
.about-intro, .about-success {
  padding: 80px 20px;
  text-align: center;
  background: #f8fafc;
  p { max-width: 900px; margin: auto; font-size: 1.1rem; line-height: 1.7; color: #1e293b; }
}

.about-journey, .about-approach {
  padding: 80px 20px;
  h2 { text-align: center; font-size: 2rem; margin-bottom: 40px; color: #1e40af; }

  .journey-cards, .approach-cards {
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
    justify-content: center;
  }

  .journey-card, .approach-card {
    background: #fff;
    padding: 30px;
    border-radius: 12px;
    box-shadow: 0 10px 25px rgba(0,0,0,0.06);
    flex: 1 1 280px;
    max-width: 350px;
    h3 { color: #1e40af; margin-bottom: 15px; font-size: 1.25rem; }
    p { color: #475569; font-size: 0.95rem; line-height: 1.6; }
  }
}

/* Mission & Vision */
/* Mission & Vision – Split Accent Layout */
.about-mission-vision {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 32px;
  padding: 100px 100px;
  background: #ffffff;

  .mission-card,
  .vision-card {
    position: relative;
    background: #ffffff;
    padding: 40px 32px;
    border-radius: 16px;
    border: 1px solid #e5e7eb;
    transition: all 0.35s ease;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 6px;
      height: 100%;
      background: linear-gradient(180deg, #3b82f6, #1e40af);
      border-radius: 16px 0 0 16px;
    }

    h2 {
      color: #0f172a;
      font-size: 1.6rem;
      margin-bottom: 16px;
    }

    p {
      font-size: 1rem;
      color: #475569;
      line-height: 1.75;
    }

    &:hover {
      transform: translateY(-8px);
      box-shadow: 0 18px 45px rgba(0, 0, 0, 0.08);
    }
  }
}

/* Core Values – Glass / Soft Contrast Grid */
.about-values {
  padding: 100px 90px;
  background: linear-gradient(135deg, #f8fafc, #eef2ff);
  text-align: center;

  h2 {
    font-size: 2.2rem;
    margin-bottom: 50px;
    color: #0f172a;
  }

  .values-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 28px;
  }

  .value-item {
    background: rgba(255, 255, 255, 0.85);
    backdrop-filter: blur(6px);
    padding: 26px 20px;
    border-radius: 14px;
    font-weight: 600;
    color: #1e293b;
    border: 1px solid rgba(99, 102, 241, 0.2);
    transition: all 0.3s ease;
    cursor: default;

    &:hover {
      transform: translateY(-6px) scale(1.02);
      border-color: #6366f1;
      box-shadow: 0 14px 35px rgba(99, 102, 241, 0.18);
    }
  }
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  .about-mission-vision,
  .about-values {
    padding: 70px 20px;
  }

  .about-values h2 {
    font-size: 1.9rem;
  }
}




/* Responsive */
@media (max-width: 768px) {
  .about-mission-vision { flex-direction: column; }
  .journey-cards, .approach-cards { flex-direction: column; }
}

.about-values {
  padding: 100px 90px;
  background: linear-gradient(135deg, #f8fafc, #eef2ff);
  text-align: center;

  h2 {
    font-size: 2.2rem;
    margin-bottom: 50px;
    color: #0f172a;
  }

  .values-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 28px;
  }

  .value-item {
    background: #ffffff;
    padding: 30px 22px;
    border-radius: 16px;
    border: 1px solid #e5e7eb;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 14px;
    transition: all 0.3s ease;

    i {
      font-size: 2rem;
      color: #3b82f6;
      transition: transform 0.3s ease, color 0.3s ease;
    }

    span {
      font-weight: 600;
      color: #1e293b;
    }

    &:hover {
      transform: translateY(-8px);
      box-shadow: 0 18px 40px rgba(59, 130, 246, 0.2);

      i {
        color: #1e40af;
        transform: scale(1.15);
      }
    }
  }
}
