
/* Hero Section */
.hero-section {
  position: relative;
  height: 63.7vh;
  width: 100%;
  overflow: hidden;
    clip-path: polygon(
    0 0,
    100% 0,
    100% 90%,
    0 100%
  );
}

.hero-video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero-overlay {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.55);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* text-align: center; */
  padding: 20px;
}

.hero-overlay h1 {
  color: #ffffff;
  font-size: 3rem;
  font-weight: 600;
  margin-bottom: 15px;
}

.hero-overlay p {
  color: #e5e7eb;
  font-size: 1.2rem;
  max-width: 700px;
}

/* Intro Section */
.intro-section {
  max-width: 1100px;
  margin: 80px auto 40px;
  padding: 0 20px;
  text-align: center;
}

.intro-section h2 {
  font-size: 2.2rem;
  margin-bottom: 15px;
  color: #111827;
}

.intro-section p {
  font-size: 1.05rem;
  color: #4b5563;
}

/* Services Grid */
.services-grid {
  max-width: 1200px;
  margin: 40px auto 80px;
  padding: 0 20px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 25px;
}

.service-card {
  background: #f9fafb;
  border-radius: 10px;
  padding: 30px 25px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  border: 1px solid #e5e7eb;
}

.service-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 12px 25px rgba(0, 0, 0, 0.08);
}

.service-card h3 {
  font-size: 1.25rem;
  margin-bottom: 12px;
  color: #111827;
}

.service-card p {
  font-size: 0.95rem;
  color: #4b5563;
}

/* CTA Section */
.cta-section {
  background: linear-gradient(135deg, #0f172a, #1e293b);
  color: #ffffff;
  text-align: center;
  padding: 80px 20px;
}

.cta-section h2 {
  font-size: 2.3rem;
  margin-bottom: 15px;
}

.cta-section p {
  font-size: 1.05rem;
  max-width: 700px;
  margin: 0 auto 30px;
  color: #d1d5db;
}

.cta-button {
  background: #ffffff;
  color: #0f172a;
  border: none;
  padding: 14px 32px;
  font-size: 1rem;
  border-radius: 30px;
  cursor: pointer;
  font-weight: 600;
  transition: background 0.3s ease, transform 0.2s ease;
}

.cta-button:hover {
  background: #e5e7eb;
  transform: translateY(-2px);
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  .hero-overlay h1 {
    font-size: 2.2rem;
  }

  .hero-overlay p {
    font-size: 1rem;
  }

  .cta-section h2 {
    font-size: 1.8rem;
  }
}


/* Service Card */
.service-card {
  position: relative;
  height: 120px;
  background: #f9fafb;
  border: 1px solid #e5e7eb;
  border-radius: 8px; /* rectangular, not rounded */
  overflow: hidden;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  align-items: center;
  justify-content: center;
}

.service-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 14px 30px rgba(0, 0, 0, 0.1);
}

/* Front (Icon + Title) */
.service-front {
  height: 100%;
  display: flex;
  /* flex-direction: row; */
  align-items: center;
  justify-content: center;
  text-align: center;
  transition: opacity 0.3s ease, transform 0.3s ease;
}

.service-icon {
  font-size: 2.5rem;
  /* margin-bottom: 12px; */
    color: #1a46a5;
}

.service-front h3 {
  font-size: 1.1rem;
  color: #111827;
  padding: 0 10px;
}

/* Back (Description) */
.service-back {
  position: absolute;
  inset: 0;
  background: #bbcbf1;
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  text-align: center;
  opacity: 0;
  transform: translateY(10px);
  transition: opacity 0.3s ease, transform 0.3s ease;
}

.service-back p {
  font-size: 0.95rem;
  line-height: 1.6;
}

/* Hover Effects */
.service-card:hover .service-front {
  opacity: 0;
  transform: translateY(-10px);
}

.service-card:hover .service-back {
  opacity: 1;
  transform: translateY(0);
}
